.home-container {
    text-align: center;
    padding: 40px;
    background-color: #f4f4f9;
  }
  
  .home-container h1 {
    font-size: 3rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  .home-container p {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 40px;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    justify-items: center;
    margin-top: 40px;
  }
  
  .card {
    background-color: #4CAF50;
    color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    text-align: center;
    font-size: 1.5rem;
  }
  
  .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .card h2 {
    font-size: 1.8rem;
    margin: 0;
  }
  